<template>
  <div class="hello">
    <form class="formbox">
      <h2>图书管理系统</h2>
      <p>书籍名称：<input type="text" placeholder="请输入书籍名称" name="bookname" v-model="bookname"></p>
      <p>书籍作者：<input type="text" placeholder="请输入书籍作者" name="bookauthor" v-model="bookauthor"></p>
      <p>出版时间：<input type="text" placeholder="请输入出版时间" name="submissiontime" v-model="submissiondate"></p>
      <div class="subtn" @click="addbook">提交</div>
    </form>

    <div class="list">
    	<h2 class='qrybtn' @click='querybook'>查询书库所有书籍</h2>
    </div>
    <div class="list">
    	<input type="text" name='qryby' v-model='qryby' placeholder="请输入查询条件">
    	<h2 class='qrybtn' @click='querybookby'>开始查询</h2>
    </div>
    <div class="list">
    	<ul>
    		<li v-for='item in booklist'>
    			<p>书名：{{item.book_title}}</p>
    			<span>作者：{{item.book_author}}</span>
    			<span>出版时间：{{item.submission_date.substr(0,10)}}</span>
    		</li>
    	</ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      qryby:'',
      bookname: '',
      bookauthor: '',
      submissiondate:'',
      booklist:[]
    }
  },
  methods: {
    addbook() {
      this.$http.post('/api/bookinfo/addbook', {
        book_title: this.bookname,
        book_author: this.bookauthor,
        submission_date:this.submissiondate
      },{}).then((response) => {
        console.log(response);
      })
    },
    querybook() {
    	this.$http.post('/api/bookinfo/qrybookinfo').then((res)=>{
    		console.log(res )
    		this.booklist = res.data
    	}) 
    },
    querybookby() {
    	this.$http.post('/api/bookinfo/qrybookinfoby',{
    		book_author: this.qryby
    	}).then((res)=>{
    		console.log(res)
    		this.booklist = res.data
    	}) 
    }
  }
}
</script>
<style lang="stylus" scoped>
.formbox
	font-size 0.16em
	h2
		font-size 0.2rem
		margin 0.5rem auto 0.3rem auto
	.subtn
		width 0.8rem
		height 0.4rem
		line-height 0.4rem
		text-align center 
		background #1507e4
		color #fff
		margin 0 auto
		border-radius 0.05rem
input
	padding 0 0.05rem
	height 0.3rem
	line-height 0.3rem
	width 2rem
	margin 0.1rem auto
	border 1px solid #ccc
	border-radius 0.05rem
.list
	font-size 0.16em
	.qrybtn
		font-size 0.16em
		width 2rem
		height 0.4rem	
		line-height 0.4rem
		text-align center 
		background #1507e4
		color #fff
		margin 0.5rem auto
		border-radius 0.05rem
	li
		height 0.5rem
		line-height 0.24rem
		background #ccc
		margin-top 0.05rem
		padding 0.05rem 0

</style>


